.button {
  height: 38px;
  padding: 5px 15px;

  color: #dc3545;
  font-family: inherit;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #dc3545;
  background-color: #fff;
  transition: 0.5s;
}

.button:hover {
  cursor: pointer;
  color: #fff;
  background-color: #dc3545
}